.list-item {
  display: flex;
  justify-content: space-between;
}
.list-item .todo-item {
  display: flex;
  align-items: center;
}
.list-item .todo-item .todo-tag {
  margin: 10px;
  white-space: normal !important;
  max-width: 130px;
  word-break: break-all;
}
@media only screen and (min-width: 576px) and (max-width: 767px) {
  .list-item .todo-item .todo-tag {
    max-width: 500px;
  }
}
@media only screen and (min-width: 768px) {
  .list-item .todo-item .todo-tag {
    max-width: 600px;
  }
}
